.menu__filter {
    display: flex;
}

.menu__ingredients {
    height: calc(100vh - 85px - 40px - 165px);
    width: 600px;
    overflow: auto;
}

.menu__ingredients::-webkit-scrollbar {
    width: 8px;
    height: 400px;
}

.menu__ingredients::-webkit-scrollbar-thumb {
    width: 8px;
    background-color: #8585AD;
}

.menu__ingredients::-webkit-scrollbar-track {
    background: #2F2F37;
    margin-top: 40px;
    margin-bottom: 40px;
}